import React from 'react'
import Helmet from 'react-helmet'

const Seo = ({ title, metaDescription }) => {
  return <Helmet 
  htmlAttributes={`en`}
  title={title}
  xmlns="w3.org/1999/xhtml"
  meta={[
    {
      name: `description`,
      content: metaDescription,
    },
    {
      property: `og:title`,
      content: title,
    },
    {
      property: `og:description`,
      content: metaDescription,
    },
    {
      property: `og:image`,
      content: `/src/images/icon.png`,
    },
    {
      property: `og:type`,
      content: `website`,
    },
    {
      property: `og:url`,
      content: `https://gmaesopsfables.org`,
    },
    {
      name: `twitter:card`,
      content: `summary`,
    },
    {
      name: `twitter:title`,
      content: title,
    },
    {
      name: `twitter:description`,
      content: metaDescription,
    },
    {
      name: `twitter:image`,
      content: `/src/images/icon.png`,
    },
  ]}
  />
}

export default Seo